
.page-error {
  color: #707473;
  text-align: center;
}
.page-error .box {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 360px;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}



